<template>
  <div class="tasks">
    <div class="team-section">
      <button class="move-button" v-if="showPrevTeamButton" @click="prevTeam">◀</button>
      <h1 v-if="teams.length > 0">{{ teams[teamIndex].teamName }}</h1>
      <button class="move-button" v-if="showNextTeamButton" @click="nextTeam">▶</button>
    </div>
    <div class="date-picker">
      <input type="date" v-model="selectedDate" @change="fetchTasks">
    </div>

    <div class="goals-container">
      <div class="left-side box">
        <div class="left-side-header">
          <button class="move-button" v-if="showPrevPlanButton" @click="prevPlan">◀</button>
          <h2 v-if="plan.title">{{ plan.title }}</h2>
          <button class="move-button" v-if="showNextPlanButton" @click="nextPlan">▶</button>
        </div>
        <div class="chart-container">
          <span class="goal-report" v-for="(goal) in goals" :key="goal">
            <span style="width: 40%;">
              <b>{{ goal.title }}</b>
              <span @click="openWriteShareModal(goal)">
                <i class="fas fa-share share-icon" style="color: grey"></i>
              </span>
            </span>
            <span style="width: 60%;">
              <img v-for="(opacity, index) in goal.imageArray"
                   :key="index"
                   :style="{ opacity: opacity }"
                   src="@/assets/badge-perfect.png" alt="">
              </span>
            </span>
        </div>
      </div>
      <div class="right-side box">
        <div class="self-feedback box">
          <div class="self-feedback-header">
            <h2>셀프 피드백</h2>
            <div>
              <span class="icon-wrapper" @click="openSelfFeedbackModal">
                <i class="fas fa-pencil-alt"></i>
              </span>
              <span class="icon-wrapper" @click="deleteSelfFeedback">
                <i class="fas fa-trash-alt"></i>
              </span>
            </div>
          </div>
          <div class="self-feedback content">
            <span>{{ self.reason }}</span>
          </div>
        </div>

        <div class="manager-feedback box">
          <div class="manager-feedback-header">
            <h2>팀장 피드백</h2>
            <div>
              <span @click="openFeedbackModal">
                <i class="fas fa-pencil-alt"></i>
              </span>
              <span @click="deleteFeedback">
                <i class="fas fa-trash-alt"></i>
              </span>
            </div>
          </div>
          <div class="manager-feedback content">
            <span>{{ this.feedback.comment }}</span>
          </div>
        </div>

        <div v-if="selfModalVisible" class="goal-view-modal" @click.stop @keydown.stop>
          <div class="goal-view-modal-content">
            <span class="close" @click="closeSelfFeedbackModal">&times;</span>
            <h2>셀프피드백 작성</h2>
            <p>피드백 내용을 입력하세요:</p>
            <textarea v-model="self.reason" rows="4" cols="50"></textarea>
            <br>
            <button @click="submitSelfFeedback">저장</button>
          </div>
        </div>

        <div v-if="feedbackModalVisible" class="goal-view-modal" @click.stop @keydown.stop>
          <div class="goal-view-modal-content">
            <span class="close" @click="closeFeedbackModal">&times;</span>
            <h2>피드백 작성</h2>
            <p>피드백 내용을 입력하세요:</p>
            <textarea v-model="feedback.comment" rows="4" cols="50"></textarea>
            <br>
            <button @click="submitFeedback">저장</button>
          </div>
        </div>

        <div v-if="showShareModal===1" class="goal-view-modal" @click.stop @keydown.stop>
          <div class="goal-view-modal-content">
            <span class="close" @click="closeShareModal">&times;</span>
            <h3>오늘은 무엇을 하셨나요?</h3>
            <p>달성 계획: {{ shareGoal.title }}</p>
            <textarea v-model="shareGoal.content" rows="2" cols="20"></textarea>
            <br>
            <button @click="shareProgress">공유하기</button>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all.js'

// TODO: 날짜선택, 팀 변경
export default {
  name: 'GoalsView',
  data() {
    return {
      teamIndex: 0,
      member: {},
      selectedDate: null,
      chart: null,
      teams: [],
      goals: [],
      plan: {},

      planIndex: 0,
      feedback: {},
      self: {},
      selfModalVisible: false,
      feedbackModalVisible: false,
      isTutor: false,

      showPrevTeamButton: true,
      showNextTeamButton: true,
      showPrevPlanButton: true,
      showNextPlanButton: true,

      shareGoal: {},
      showShareModal: 0,
    };
  },
  computed: {
    formattedDate() {
      const date = new Date();
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, "0");
      const day = String(date.getDate()).padStart(2, "0");
      return `${year}-${month}-${day}`;
    }
  },
  mounted() {
    this.getTeamList();
    this.selectedDate = this.formattedDate;

  },
  methods: {
    getTeamList() {
      axios.get(apiURL + '/team/my-list', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        this.teams = response.data;
        this.setTutorButton();
        this.fetchTasks();
        this.prevTeamButton();
        this.nextTeamButton();
        this.prevPlanButton();
        this.nextPlanButton();
      }).catch((error) => {
        console.log(error);
      });
    },
    fetchTasks() {
      if (this.teams.length === 0) {
        return;
      }

      if (this.teams[this.teamIndex].planDtoList.length === 0) {
        return;
      }
      const formattedDate = new Date().toISOString().split('T')[0]; // 날짜를 'yyyy-MM-dd' 형식으로 변환합니다.

      const planId = this.teams[this.teamIndex].planDtoList[this.planIndex].id;
      axios.get(apiURL + '/report/plan/' + planId + '/date/' + formattedDate, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        this.plan = response.data.plan;
        this.goals = response.data.plan.goalList;
        this.feedback = response.data.feedback;
        this.self = response.data.selfFeedback;

        if (this.self.reason === null) {
          this.self.reason = '작성된 셀프 피드백이 없습니다.';
        }
        if (this.feedback.comment === null) {
          this.feedback.comment = '작성된 팀장 피드백이 없습니다.';
        }

        this.drawAchievementChart();
      }).catch((error) => {
        console.log(error);
      });
    },

    setTutorButton() {
      if (this.teams[this.teamIndex].gradeName === 'OWNER' ||
          this.teams[this.teamIndex].gradeName === 'MANAGER') {
        this.isTutor = true;
      }
    },
    openFeedbackModal() {
      this.feedbackModalVisible = !this.feedbackModalVisible;
    },
    closeFeedbackModal() {
      this.feedbackModalVisible = !this.feedbackModalVisible;
    },
    async deleteFeedback() {
      if (!confirm('피드백을 삭제하시겠습니까?')) {
        return;
      }
      if (this.feedback.id === null) {
        alert('삭제할 피드백이 없습니다.');
        return;
      }
      try {
        await axios.delete(apiURL + '/feedback/' + this.teams[this.teamIndex].id + '/feedback/' + this.feedback.id, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('user-token')}`,
          },
        });
      } catch (error) {
        console.log(error);
      }
    },

    submitFeedback() {
      if (this.feedback.id !== null) {
        axios.put(apiURL + '/feedback/' + this.teams[this.teamIndex].id + '/feedback/', {
          id: this.feedback.id,
          teamId: this.teams[this.teamIndex].id,
          comment: this.feedback.comment,
          checkDate: this.selectedDate,
        }, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('user-token')}`,
          },
        }).then((response) => {
          this.feedback = response.data;
          this.closeFeedbackModal();
        }).catch((error) => {
          console.log(error);
        });

        return;
      }

      axios.post(apiURL + '/feedback/', {
        planId: this.teams[this.teamIndex].planDtoList[this.planIndex].id,
        comment: this.feedback.comment,
        checkDate: this.selectedDate,
      }, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        console.log(response);
        this.feedback = response.data;
        this.closeFeedbackModal();
      }).catch((error) => {
        console.log(error);
      });
    },
    openSelfFeedbackModal() {
      this.selfModalVisible = !this.selfModalVisible;
      console.log(this.selfModalVisible);
    },
    closeSelfFeedbackModal() {
      this.selfModalVisible = !this.selfModalVisible;
    },
    deleteSelfFeedback() {
      if (!confirm('셀프 피드백을 삭제하시겠습니까?')) {
        return;
      }

      if (this.self.id === null) {
        alert('삭제할 셀프 피드백이 없습니다.');
        return;
      }

      console.log('deleteSelfFeedback');
      axios.delete(apiURL + '/goal/' + this.teams[this.teamIndex].id + '/self/' + this.self.id, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        console.log(response);
        this.self = {reason: '작성된 셀프 피드백이 없습니다.'};
      }).catch((error) => {
        console.log(error);
      });
    },
    submitSelfFeedback() {
      if (this.self.id !== null) {
        axios.put(apiURL + '/feedback/self/', {
          id: this.self.id,
          planId: this.teams[this.teamIndex].planDtoList[this.planIndex].id,
          reason: this.self.reason,
          measure: '123',
          checkDate: this.selectedDate,
        }, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('user-token')}`,
          },
        }).then((response) => {
          console.log(response);
          this.closeSelfFeedbackModal();
        }).catch((error) => {
          console.log(error);
        });

        return;
      }

      axios.post(apiURL + '/feedback/self', {
        planId: this.teams[this.teamIndex].planDtoList[this.planIndex].id,
        reason: this.self.reason,
        measure: '',
        checkDate: this.selectedDate,
      }, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        console.log(response);
        this.self = response.data;
        this.closeSelfFeedbackModal();
      }).catch((error) => {
        console.log(error);
      });
    },

    drawAchievementChart() {
      this.goals.forEach((goal) => {
        goal.imageArray = [];
        for (let i = 0; i < goal.dailyCheckList.length; i++) {
          goal.imageArray.push(1);
        }
        for (let i = goal.dailyCheckList.length; i < goal.goalCount; i++) {
          goal.imageArray.push(0.2);
        }
      });

    },

    prevTeamButton() {
      this.showPrevTeamButton = (this.teamIndex !== 0);
    },
    prevTeam() {
      this.teamIndex--;
      this.planIndex = 0;
      this.fetchTasks();
      this.prevTeamButton();
      this.nextTeamButton();
    },
    nextTeamButton() {
      this.showNextTeamButton = this.teamIndex !== this.teams.length - 1;
    },
    nextTeam() {
      this.teamIndex++;
      this.planIndex = 0;
      this.fetchTasks();
      this.prevTeamButton();
      this.nextTeamButton();
    },

    prevPlanButton() {
      this.showPrevPlanButton = (this.planIndex !== 0);
    },
    prevPlan() {
      this.planIndex--;
      this.fetchTasks();
      this.prevPlanButton();
      this.nextPlanButton();
    },
    nextPlanButton() {
      this.showNextPlanButton =
          this.planIndex !== this.teams[this.teamIndex].planDtoList.length - 1;
    },
    nextPlan() {
      this.planIndex++;
      this.fetchTasks();
      this.prevPlanButton();
      this.nextPlanButton();
    },

    openWriteShareModal(goal) {
      this.shareGoal.title = goal.title;
      this.showShareModal = 1;
    },
    openConfirmShareModal() {
      this.showShareModal = 2;
    },
    closeShareModal() {
      this.showShareModal = 0;
    },
    shareProgress() {
      axios.post(apiURL + '/sns/write', this.shareGoal, {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('user-token')}`,
        },
      }).then((response) => {
        console.log(response);
        alert('공유되었습니다.');
        this.closeShareModal();
      }).catch((error) => {
        console.log(error);
      });
    },
  },
}

</script>

<style scoped>
.team-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.move-button {
  margin: 0 10px;
  padding: 5px;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 0.5rem;
  color: #42b983;
}

.icon-wrapper {
  display: inline;
}

.tasks {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.date-picker {
  margin: 20px 0;
}

input[type="date"] {
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  padding: 8px 12px;
  color: #333;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  transition: border-color 0.2s, box-shadow 0.2s;
}

input[type="date"]:hover {
  border-color: #999;
}

input[type="date"]:focus {
  border-color: #4a8df8;
  box-shadow: 0 0 5px rgba(74, 137, 248, 0.5);
}

img {
  width: 20px;
  height: 22px;
  margin-left: 5px;
}


.goals-container {
  display: flex;
  width: 100%;
  height: 100%;
}

.left-side,
.right-side {
  width: 100%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.left-side-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content: center;
}

.chart-container {
  width: 100%; /* 차트 너비를 조절합니다. */
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.self-feedback,
.manager-feedback {
  flex: 1;
  padding: 15px;
  box-sizing: border-box;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 15px;
}

.self-feedback-header,
.manager-feedback-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 10px;
  padding: 30px;
}


.content {
  font-size: 10px;
  line-height: 1.5;
  text-align: left;
}

.box {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 10px;
}


/* 모달 스타일 */
.goal-view-modal {
  display: flex;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.goal-view-modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 600px;
}

/* 닫기 버튼 스타일 */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.goal-report {
  width: 100%;
  display: flex;
  align-items: start;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 15px;
}


/* 모바일 레이아웃 */
@media (max-width: 767px) {
  .goals-container {
    flex-direction: column;
  }

  .left-side,
  .right-side {
    width: 100%;
  }

  .right-side {
    flex-direction: column;
  }
}

/* 태블릿 레이아웃 */
@media (min-width: 768px) and (max-width: 1024px) {
  .goals-container {
    flex-direction: row;
  }

  .left-side,
  .right-side {
    width: 50%;
  }

  .right-side {
    flex-direction: column;
  }
}

/* 데스크톱 레이아웃 */
@media (min-width: 1025px) {
  .goals-container {
    flex-direction: row;
  }

  .left-side,
  .right-side {
    width: 50%;
  }

  .right-side {
    flex-direction: column;
  }
}

</style>
