<template>
  <div class="team-list owner">
    <h3>가입한 팀</h3>
    <div class="team" v-for="member in teamMembers" :key="member.id">
      <div>
        <div class="team-title-section" v-if="member.gradeName === 'MEMBER'">
          <router-link class="team-name-link" :to="`/my-teams/detail/${member.teamId}/teamMemberId/1`">
            <h3>{{ member.teamName }}</h3>
          </router-link>

          <span class="icon-wrapper" @click="withdrawal(member)">
                <i class="fas fa-trash-alt"></i>
            </span>
        </div>
        <div class="date-section">
          <font-awesome-icon :icon="['fas', 'calendar-days']"/>
          {{ member.startDate }} ~ {{ member.endDate }}
        </div>
        <p>{{ member.description }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "MyMemberTeam",
  data() {
    return {
      apiUrl: apiURL,
      teamMembers: [],
      showUpdateModal: false,
      selectedTeam: null,
      updateTeamIsOpen: false,
    };
  },
  async mounted() {
    try {
      const response = await axios.get(this.apiUrl + '/team/my-list?type=MEMBER', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        },
      });
      this.teamMembers = response.data;
    } catch (error) {
      console.error('Error fetching team list:', error);
    }
  },
  methods: {
    async updateTeam(team) {
      const teamDto = {
        id: team.id,
        teamName: document.getElementsByClassName('team-name')[0].value,
        description: document.getElementsByClassName('description')[0].value,
        isOpen: this.updateTeamIsOpen,
      };

      try {
        await axios.put(this.apiUrl + '/team/update', teamDto, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });

        const teamIndex = this.teamMembers.findIndex(t => t.id === team.id);
        if (teamIndex !== -1) {
          this.teamMembers[teamIndex].name = document.getElementsByClassName('team-name')[0].value;
          this.teamMembers[teamIndex].description = document.getElementsByClassName('description')[0].value;
        }

        alert('팀 수정 성공');
        this.closeUpdateModal();
      } catch (error) {
        console.error('Error updating team:', error);
      }
    },
    async cancelJoin(member) {
      console.log(member.id);
      if (!confirm('가입을 취소하시겠습니까?')) {
        return;
      }

      axios.delete(this.apiUrl + '/team/leave/' + member.id, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      }).then(() => {
        alert('가입 취소 성공');
        this.teamMembers = this.teamMembers.filter(teamMember => teamMember.id !== member.id);
      }).catch(error => {
        console.error('Error canceling join:', error);
      });

    },
    async withdrawal(member) {
      if (!confirm('정말로 팀을 탈퇴하시겠습니까?')) {
        return;
      }

      try {
        const response = await axios.delete(apiURL + '/team/leave/' + member.id, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });

        alert('팀 탈퇴가 완료되었습니다.');
        const deleteMemberIndex = this.teamMembers.findIndex(m => m.id === member.id);
        if (deleteMemberIndex !== -1) { // findIndex는 일치하는 요소가 없을 경우 -1을 반환합니다.
          this.teamMembers.splice(deleteMemberIndex, 1);
        }
        console.log(response);
      } catch (e) {
        console.log(e);
      }


    },
  },

};
</script>

<style scoped>
.team-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-title-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date-section {
  padding: 5px 12px;
  font-size: 12px;
  color: #6699CC;
}

.team {
  border: 2px solid #20c997;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  width: 80%;
}

.team-name {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 5px;
}

.description {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.team-name-link h3 {
  text-decoration: none;
  display: inline-block;
  padding: 10px 16px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: lightslategray;
  background-color: transparent;
  transition: background-color 0.3s;
}

.team-name-link h3:hover {
  background-color: #20c997;
  color: #fff;
}
</style>