<template>
  <div class="team-list owner">
    <h3>가입대기 중인 팀</h3>
    <div class="team" v-for="member in teamMembers" :key="member.id">
      <div class="team-title-section"
           v-if="member.gradeName === 'PENDING'">
        <h3>{{ member.teamName }}</h3>
        <button class="team-cancel-join-button" @click="cancelJoin(member)">가입 취소</button>
      </div>
      <div class="date-section">
        <font-awesome-icon :icon="['fas', 'calendar-days']"/>
        {{ member.startDate }} ~ {{ member.endDate }}
      </div>
      <p>{{ member.description }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "MyMemberTeam",
  data() {
    return {
      apiUrl: apiURL,
      teamMembers: [],
      showUpdateModal: false,
      selectedTeam: null,
      updateTeamIsOpen: false,
    };
  },
  async mounted() {
    try {
      const response = await axios.get(this.apiUrl + '/team/my-list?type=PENDING', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        },
      });
      this.teamMembers = response.data;
      console.log('Team list:', this.teamMembers);
      console.log(this.teamMembers[0].id);
    } catch
        (error) {
      console.error('Error fetching team list:', error);
    }
  },
  methods: {
    async cancelJoin(member) {
      console.log(member.id);
      if (!confirm('가입을 취소하시겠습니까?')) {
        return;
      }

      axios.delete(this.apiUrl + '/team/leave/' + member.id, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      }).then(() => {
        alert('가입 취소 성공');
        this.teamMembers = this.teamMembers.filter(teamMember => teamMember.id !== member.id);
      }).catch(error => {
        console.error('Error canceling join:', error);
      });

    }

  },

};
</script>

<style scoped>

.team-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-title-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.team {
  border: 2px solid #20c997;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  width: 80%;
}

.team-name {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.description {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.team-cancel-join-button {
  background-color: #42b983;
  border: none;
  color: #fff;
  padding: 8px 8px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

.team-name-link h3 {
  text-decoration: none;
  display: inline-block;
  padding: 10px 16px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: lightslategray;
  background-color: transparent;
  transition: background-color 0.3s;
}

.team-name-link h3:hover {
  background-color: #20c997;
  color: #fff;
}

.date-section {
  padding: 5px 12px;
  font-size: 12px;
  color: #6699CC;
}
</style>