<template>
  <div class="plan-card">
    <div class="title-section">
      <h3 @click="openPlanDetailModal">{{ plan.title }}</h3>
      <span class="plan-cancel-icon" @click="deletePlan">&times;</span>
    </div>
    <div class="content-section">
      <ul>
        <li v-for="(g, index) in plan.goalList" :key="index">
          <span class="goal-content" @click="openModifyGoalModal(g)">{{ g.title }} ({{ g.goalCount }}회)</span>
          <span class="goal-cancel-icon" @click="deleteGoal(plan.id ,g.id)">&times;</span>
        </li>
      </ul>
      <button @click="changeAddGoalSection">+</button>
    </div>
    <div v-if="showAddGoalSection" class="add-goal-section">
      <input
          class="add-goal-name"
          v-model="newGoal.title"
          @input="updateNewGoalTitle"
          placeholder="목표"/>
      <input
          class="add-goal-count"
          v-model="newGoal.goalCount"
          type="number" min="0" max="7" placeholder="목표 횟수"/>
      <button class="add-goal-button" @click="addGoal">추가</button>
    </div>

    <!-- 계획 상세보기 모달 -->
    <div class="modal" v-if="showPlanDetail">
      <div class="modal-content">
        <span class="cancel-icon" @click="closePlanDetailModal">&times;</span>
        <h3> 계획 수정하기 </h3>
        <input v-model="updatedPlan.title" name="updatedPlanTitle" class="tag-input">
        <input
            name="updatedPlanDescription"
            type="text"
            v-model="updatedPlan.description"
            class="tag-input"
        />
        <button class="tag-input-button" @click="updatePlan">수정하기</button>
      </div>
    </div>

    <!-- 목표 수정하기 모달 -->
    <div class="modal" v-if="showModifyGoalModal">
      <div class="modal-content">
        <span class="cancel-icon" @click="closeModifyGoalModal">&times;</span>
        <h3> 목표 수정하기 </h3>
        <input v-model="modifiedGoal.title" name="updatedPlanTitle" class="tag-input">
        <input
            v-model="modifiedGoal.goalCount"
            class="tag-input"
            type="number" min="0" max="7" placeholder="목표 횟수"
        />
        <button class="tag-input-button" @click="modifyGoal">수정하기</button>
      </div>
    </div>
  </div>
</template>

<script>
import {apiURL} from "@/services/apiService";
import axios from "axios";

export default {
  data() {
    return {
      member: {},
      newGoal: {},
      updatedPlan: {},
      modifiedGoal: {},

      showPlanDetail: false,
      showAddGoalSection: false,
      showModifyGoalModal: false,
    };
  },
  props: {
    plan: {
      type: Object,
      required: true,
    },
  },
  methods: {
    async updatePlan() {
      console.log(this.updatedPlan);
      try {
        await axios.put(apiURL + '/plan', this.updatedPlan, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
          }
        });
        alert('계획 수정이 완료되었습니다');
        this.closePlanDetailModal();
      } catch (e) {
        console.log(e);
      }
    },
    async deletePlan() {
      if (!confirm('피드백과 목표정보가 전부 삭제됩니다. 정말로 삭제하시겠습니까?')) return;
      try {
        const response = await axios.delete(apiURL + '/plan/' + this.plan.id, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
          }
        });
        alert(response.data);
        this.$emit('delete-plan', this.plan.id);
      } catch (e) {
        console.log(e);
      }

    },
    async addGoal() {
      this.newGoal.planId = this.plan.id;
      try {
        const response = await axios.post(apiURL + '/goal', this.newGoal, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
          }
        });
        this.updateGoal(response.data);
      } catch (e) {
        console.error(e);
      }

      this.newGoal = {};
    },
    async deleteGoal(planId, goalId) {
      if (!confirm('정말 삭제하시겠습니까?')) return;
      const response = await axios.delete(apiURL + '/goal/' + goalId, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
        }
      });

      alert(response.data);
      this.$emit('delete-goal', planId, goalId);
    },
    async modifyGoal() {
      console.log(this.modifiedGoal);
      try {
        const response = await axios.put(apiURL + '/goal', this.modifiedGoal, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
          }
        });
        alert(response.data);
        this.closeModifyGoalModal();
      } catch (e) {
        console.log(e);
      }
    },
    updateGoal(responseGoal) {
      this.$emit('update-goal', responseGoal);
    },
    updateNewGoalTitle(event) {
      this.newGoal.title = event.target.value;
    },
    openPlanDetailModal() {
      this.updatedPlan = this.plan;
      this.showPlanDetail = true;
    },
    closePlanDetailModal() {
      this.showPlanDetail = false;
    },
    changeAddGoalSection() {
      this.showAddGoalSection = !this.showAddGoalSection;
    },
    openModifyGoalModal(goal) {
      this.showModifyGoalModal = true;
      this.modifiedGoal = goal;
    },
    closeModifyGoalModal() {
      this.showModifyGoalModal = false;
    },
  },
};
</script>

<style scoped>
.plan-card {
  border-radius: 20px;
  padding: 20px;
  background-color: #f8f9fa;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 10px;
}

.plan-card h3 {
  margin-bottom: 10px;
  margin-top: 10px;
  flex: 1; /* 추가: 제목 영역이 가능한 한 넓게 펼쳐지도록 설정 */
}

.plan-card ul {
  list-style-type: none;
  padding: 0;
}

.plan-cancel-icon {
  flex: none; /* 추가: 취소 아이콘은 자신의 크기만큼 유지되도록 설정 */
  margin: 10px; /* 추가: 취소 아이콘과 제목 사이의 여백 설정 */
  cursor: pointer;
  font-size: 10pt;
  color: navy;
}

.title-section {
  background-color: #e9ecef;
  padding: 0;
  border-radius: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.content-section {
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 10px;
  width: 100%;
  margin-top: 10px;
}


.content-section button {
  margin: 0 10px;
  padding: 5px;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 0.5rem;
  color: #42b983;
}

.add-goal-section {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  width: 100%;
}

.add-goal-section input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ced4da;
}

.add-goal-name {
  width: 70%;
}

.add-goal-count {
  width: 20%;
}

.add-goal-button {
  padding: 1px 5px;
  border: none;
  background-color: #6699CC;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 10pt;
  width: 20%;
  height: 30px;

}

.add-goal-section input {
  height: 30px;
}

.goal-content {
  font-size: 10pt;
}

.goal-cancel-icon {
  cursor: pointer;
  font-size: 10pt;
  color: #ced4da;
  padding-left: 5px;
}


/* 모달 스타일 */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.tag-input-button {
  background-color: #00b9b9;
  border: none;
  color: white;
  padding: 12px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 8px 0;
  cursor: pointer;
  border-radius: 5px;
}

.tag-input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

</style>
