<!-- TeamOwnerDetailView.vue -->
<template>
  <div class="container">
    <h2 style="color: grey; padding-bottom: 10px;">Team Members</h2>
    <ul class="list-group">
      <li class="member-name-link"
          v-for="(member, index) in teamMembers" :key="index">
        <router-link
            :to="`/my-teams/detail/${teamId}/teamMemberId/${member.id}`"
            :memberId="member.id"
        >
          <h4>{{ member.nickname }}</h4>
        </router-link>
        <span @click="openChangeGradeModal(member)"
              class="badge rounded-pill badge-backcolor">
          {{ member.gradeName }}
        </span>
      </li>
    </ul>
  </div>

  <div v-if="showChangeGradeModal" class="modal">
    <div class="modal-content">
      <span class="cancel-icon" @click="closeChangeGradeModal">&times;</span>
      <h2 class="modal-title">역할 조정</h2>
      <ul class="badge-list">
        <li class="badge-list-item" v-for="badge in badgeList" :key="badge.id">
            <span class="badge-icon" @click="changeGrade(badge)">
              {{ badge.name }}
            </span>
        </li>
      </ul>
    </div>
  </div>

</template>
<!-- TeamOwnerDetailView.vue -->

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "TeamDetailView",
  data() {
    return {
      apiUrl: apiURL,
      teamMembers: [],
      leaderSelection: {},
      userGoals: [],
      teamId: this.$route.params.teamId,
      showChangeGradeModal: false,
      badgeList: [
        {id: 1, name: '팀장', code: 'OWNER', iconUrl: '뱃지 1 이미지 URL'},
        {id: 2, name: '매니저', code: 'MANAGER', iconUrl: '뱃지 2 이미지 URL'},
        {id: 3, name: '회원', code: 'MEMBER', iconUrl: '뱃지 3 이미지 URL'},
      ],
    };
  },
  computed: {
    // Add computed properties here
  },
  created() {
    this.fetchTeamMembers(this.teamId);
  },
  methods: {
    async fetchTeamMembers(teamId) {
      const response = await axios.get(this.apiUrl + `/team/owner/` + teamId, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        },
      });
      this.teamMembers = response.data;
    },
    showGoals(member) {
      this.selectedMember = member;
    },
    openChangeGradeModal(member) {
      this.showChangeGradeModal = true;
      this.leaderSelection = member;
    },
    closeChangeGradeModal() {
      this.showChangeGradeModal = false;
    },
    async changeGrade(badge) {
      if (!confirm(badge.name + '로 등급을 조정 하시겠어요?')) {
        return;
      }

      this.leaderSelection.gradeName = badge.code;
      try {
        await axios.put(
            apiURL + '/team/position',
            this.leaderSelection,
            {
              headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
              },
            });
        alert('등급이 조정되었습니다.');
        const memberToUpdate = this.teamMembers.find(member => member.id === this.leaderSelection.id);
        memberToUpdate.gradeName = badge.code;
        this.closeChangeGradeModal();
      } catch (e) {
        alert('시스템 오류');
        console.log(e);
      }
    }

  }
};
</script>

<style scoped>
/* Modal 스타일 */
.modal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  align-items: center;
}

/* 뱃지 리스트 스타일 */
.badge-list {
  list-style-type: none;
  padding: 0;
}

.badge-list-item {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.badge-icon {
  margin-right: 10px;
}

/* 제목 스타일 */
.modal-title {
  color: grey; /* 원하는 색상으로 변경해 주세요 */
}

.member-name-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-inline: 10px ;
}

.member-name-link h4 {
  text-decoration: none;
  display: inline-block;
  padding: 5px 8px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: #42b983;
  background-color: transparent;
  transition: background-color 0.3s;
}

.member-name-link h4:hover {
  background-color: #20c997;
  color: #fff;
}

.badge-backcolor {
  background-color: #42b983;
}
</style>