        <team-tab/>
      </div>
    </header>
    <main>
      <router-view/>
    </main>
    <FooterBar :show-team-tab="showTab" @open-tab="openTab"/>
  </div>
</template>

<script>
import HeaderBar from '@/components/home/HeaderBar.vue';
import FooterBar from "@/components/home/FooterBar.vue";
import TeamTab from "@/components/team/TeamTab.vue";

export default {
  name: 'App',
  data() {
    return {
      showTab: 'NONE',
    }
  },
  components: {
    FooterBar,
    HeaderBar,
    TeamTab,
  },
  methods: {
    openTab(tabName) {
      this.showTab = tabName;
    },
  }
}
</script>

