<template>
    <div class="left-menu">
      <font-awesome-icon @click="tabHamburgerMenu"
                         class="home-icon" :icon="['fas', 'bars']"
                         style="color: whitesmoke"/>
    </div>
    <div class="center-menu">
      <h4>모모와 원정대</h4>
    </div>
    <div class="right-menu">
      <router-link to="/profile">
        <font-awesome-icon @click="openTab" class="home-icon" :icon="['fasl', 'user']" style="color: whitesmoke;"/>
      </router-link>
      <font-awesome-icon @click="openTab" class="home-icon" :icon="['fasl', 'right-from-bracket']"
                         style="color:  whitesmoke;"/>
    </div>
    <div v-if="showHamburgerMenu" @click="tabHamburgerMenu" class="menu">
      <span>
        <font-awesome-icon
            @click="closeHamburgerMenu"
            :icon="['fas', 'xmark']" class="ham-close-button"
            style="color: #42b983;"
        />
      </span>
    </div>
</template>
<script>

export default {
  name: "HeaderBar",
  data() {
    return {
      showHamburgerMenu: false,
    }
  },
  props: {
    showTab: {
      type: String,
      required: true,
    }
  },
  methods: {
    tabHamburgerMenu() {
      this.showHamburgerMenu = !this.showHamburgerMenu;
    },
    openHamburgerMenu() {
      this.showHamburgerMenu = true;
    },
    closeHamburgerMenu() {
      this.showHamburgerMenu = false;
    },
    openTab(tabName) {
      this.$emit('open-tab', tabName);
    },
  },
}


</script>
<style scoped>
a {
  text-decoration: none; /* Removes underline */
}

a > b {
  color: #42b983; /* Changes link color to black */
}

a:hover > b {
  color: #357266; /* Changes link color to red when mouse is over the link */
}

.center-menu {
  color: whitesmoke;
}

.left-menu,
.right-menu {
  margin: 0 5px;
  padding: 5px;
  display: flex;
}

.home-icon {
  margin: 0 5px;
  padding: 3px;
}

.menu {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background-color: whitesmoke;
  padding: 20px;
  overflow: auto;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
}

.menu-list {
  display: flex;
  flex-direction: column;
}

.ham-close-button {
  float: right;
}
</style>
