<template>
  <div>
    <div class="team-section">
      <button v-if="showPrevButton" @click="prevTeam">◀</button>
      <h1>{{ selectedMember.teamName }} </h1>
      <button v-if="showNextButton" @click="nextTeam">▶</button>
      <br/>
      <button @click="openCreatePlanModal">+</button>
    </div>
    <div v-if="selectedMember">
      <div v-for="(p, index) in selectedMember.planDtoList" :key="index">
        <plan-card
            :plan='p'
            @update-goal="updateGoal"
            @delete-goal="deleteGoal"
            @delete-plan="deletePlan"
        />
      </div>
    </div>

    <!-- 계획 만들기 모달 -->
    <div class="modal" v-if="showCreatePlanModal">
      <div class="modal-content">
        <span class="cancel-icon" @click="closeCreatePlanModal">&times;</span>
        <h3> 계획 만들기 </h3>
        <input v-model="newPlan.title" placeholder="계획 이름을 입력해주세요." class="tag-input">
        <input
            type="text"
            v-model="newPlan.description"
            placeholder="계획 설명을 입력해주세요."
            class="tag-input"
        />
        <button class="tag-input-button" @click="submitPlan">만들기</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";
import PlanCard from "@/components/team/PlanCard.vue";


export default {
  name: "TeamMemberDetailView",
  data() {
    return {
      apiUrl: apiURL,
      teamMembers: [],
      selectedMember: {},
      newPlan: {},
      memberId: this.$route.params.teamMemberId,
      teamId: this.$route.params.teamId,

      showPrevButton: false,
      showNextButton: false,

      showCreatePlanModal: false,

    };
  },
  components: {
    PlanCard,
  },
  created() {
    this.fetchMember();
  },
  methods: {
    fetchMember() {
      axios.get(apiURL + '/team/member/list', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
        }
      }).then((response) => {
        console.log(response.data);
        this.teamMembers = response.data;
        this.selectedMember = this.teamMembers[0];

        this.prevButton();
        this.nextButton();
      }).catch((error) => {
        console.log(error);
      });
    },
    deletePlan(planId) {
      let selectedPlan = this.selectedMember.planDtoList.find((plan) => plan.id === planId);
      let index = this.selectedMember.planDtoList.indexOf(selectedPlan);
      this.selectedMember.planDtoList.splice(index, 1);
    },
    //TODO: 계획추가 -> 목표추가 시 화면에 반영이 잘 안됨. (새로고침하면 반영됨)
    updateGoal(newGoal) {
      console.log(newGoal);
      let selectedPlan = this.selectedMember.planDtoList[newGoal.planId - 1]
      let updatedGoal = selectedPlan.goalList.find((goal) => goal.id === newGoal.id);
      if (updatedGoal) {
        // 기존에 있던 목표를 업데이트 합니다.
      } else {
        selectedPlan.goalList.push(newGoal);
        console.log(selectedPlan);
      }
    },
    deleteGoal(planId, goalId) {
      let selectedPlan = this.selectedMember.planDtoList.find((plan) => plan.id === planId);
      let selectedGoal = selectedPlan.goalList.find((goal) => goal.id === goalId);
      let index = selectedPlan.goalList.indexOf(selectedGoal);
      selectedPlan.goalList.splice(index, 1);
    },
    prevButton() {
      this.showPrevButton = (this.selectedMember.id !== this.teamMembers[0].id);
    },
    prevTeam() {
      let index = this.teamMembers.findIndex((member) => member.id === this.selectedMember.id);
      this.selectedMember = this.teamMembers[index - 1];
      this.prevButton();
      this.nextButton();
    },
    nextButton() {
      this.showNextButton = this.selectedMember.id !== this.teamMembers[this.teamMembers.length - 1].id;
    },
    nextTeam() {
      let index = this.teamMembers.findIndex((member) => member.id === this.selectedMember.id);
      this.selectedMember = this.teamMembers[index + 1];
      this.prevButton();
      this.nextButton();
    },
    openCreatePlanModal() {
      this.showCreatePlanModal = true;
    },
    closeCreatePlanModal() {
      this.showCreatePlanModal = false;
    },
    async submitPlan() {
      this.newPlan.memberId = this.selectedMember.id;
      try {
        const response = await axios.post(apiURL + '/plan', this.newPlan, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('user-token')}`,
          }
        });
        this.selectedMember.planDtoList.push(response.data);
        this.closeCreatePlanModal();

      } catch (error) {
        console.log(error);
      }
    },

  }
};
</script>


<style scoped>
.team-section {
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.team-section button {
  margin: 0 10px;
  padding: 5px;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 0.5rem;
  color: #42b983;
}

.team-section h1 {
  font-size: 1.7rem;
  color: #777;
}

/* 모달 스타일 */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.tag-input-button {
  background-color: #00b9b9;
  border: none;
  color: white;
  padding: 12px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 8px 0;
  cursor: pointer;
  border-radius: 5px;
}

.tag-input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}


.member-name-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-inline: 10px;
}

.member-name-link h4 {
  text-decoration: none;
  display: inline-block;
  padding: 5px 8px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: #42b983;
  background-color: transparent;
  transition: background-color 0.3s;
}

.member-name-link h4:hover {
  background-color: #20c997;
  color: #fff;
}

.badge-backcolor {
  background-color: #42b983;
}
</style>