<template>
  <div class="owner">
    <div class="team-list owner">
      <h3>내가 만든 팀</h3>
      <div class="team" v-for="member in teamMembers" :key="member.id">
        <div class="team-title-section"
             v-if="member.gradeName === 'MANAGER'
               || member.gradeName === 'OWNER'">
          <router-link :to="`/my-teams/owner/detail/${member.id}`"
                       class="team-name-link"
                       :teamId="member.teamId">
            <h3>{{ member.teamName }}</h3>
          </router-link>
          <div>
                <span class="icon-wrapper" @click="openUpdateModal(member)">
                      <i class="fas fa-pencil-alt"></i>
                </span>
            <span class="icon-wrapper" @click="deleteTeam(member.id)">
                    <i class="fas fa-trash-alt"></i>
              </span>
          </div>
        </div>
        <div class="date-section">
          <font-awesome-icon :icon="['fas', 'calendar-days']"/>
          {{ member.startDate }} ~ {{ member.endDate }}
        </div>
        <p>{{ member.teamDescription }}</p>
      </div>
    </div>


  </div>

  <!-- 팀 수정 모달 -->
  <div class="modal" v-if="showUpdateModal">
    <div class="modal-content">
      <input class="tag-input-team" type="text" :value="selectedTeam.teamName">
      <input
          type="text"
          :value="selectedTeam.teamDescription"
          class="tag-input-team"
      />
      <div>
        <span style="padding: 5px">팀을 공개하실 건가요?</span>
        <input type="checkbox" @change="changeOpenOption()"/> <br/>
      </div>
      <div class="modal-button-section">
        <button class="team-update-modal-button" @click="updateTeam(selectedTeam)">수정</button>
        <button class="team-update-modal-button" @click="closeUpdateModal">닫기</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  name: "MyOwnerTeam",
  data() {
    return {
      apiUrl: apiURL,
      teamMembers: [],
      showUpdateModal: false,
      selectedTeam: null,
      updateTeamIsOpen: false,
    };
  },
  async mounted() {
    try {
      const response = await axios.get(this.apiUrl + '/team/my-list?type=OWNER', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        },
      });
      this.teamMembers = response.data;
      console.log('Team list:', this.teamMembers);
      console.log(this.teamMembers[0].id);
    } catch
        (error) {
      console.error('Error fetching team list:', error);
    }
  },
  methods: {
    openUpdateModal(team) {
      this.selectedTeam = team;
      this.showUpdateModal = true;
    },
    closeUpdateModal() {
      this.showUpdateModal = false;
    },
    changeOpenOption() {
      this.updateTeamIsOpen = !this.updateTeamIsOpen;
      console.log(this.updateTeamIsOpen);
    },

    async deleteTeam(id) {
      if (!confirm("정말 삭제하시겠습니까?")) {
        return;
      }
      console.log(id);
      try {
        await axios.delete(this.apiUrl + `/team/delete/` + id,
            {
              headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
              },
            });
        alert('팀 삭제 성공');
        this.teamMembers = this.teamMembers.filter(team => team.id !== id);
      } catch (error) {
        console.error('Error deleting team:', error);
      }

    },
    async updateTeam(team) {
      const teamDto = {
        id: team.id,
        teamName: document.getElementsByClassName('team-name')[0].value,
        description: document.getElementsByClassName('description')[0].value,
        isOpen: this.updateTeamIsOpen,
      };

      try {
        await axios.put(this.apiUrl + '/team/update', teamDto, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });

        const teamIndex = this.teamMembers.findIndex(t => t.id === team.id);
        if (teamIndex !== -1) {
          this.teamMembers[teamIndex].name = document.getElementsByClassName('team-name')[0].value;
          this.teamMembers[teamIndex].description = document.getElementsByClassName('description')[0].value;
        }

        alert('팀 수정 성공');
        this.closeUpdateModal();
      } catch (error) {
        console.error('Error updating team:', error);
      }
    },

  },

};
</script>

<style scoped>
.team-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-title-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.date-section {
  font-size: 12px;
  color: #6699CC;
}

.team {
  display: flex;
  flex-direction: column;
  border: 2px solid #20c997;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  width: 80%;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #42b983;
  width: 80%;
}

.team-name {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 5px;
}

.description {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.tag-input-team {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.modal-button-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.team-update-modal-button {
  width: 100px;
  background-color: #42b983;
  border: none;
  color: #fff;
  padding: 16px 32px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

.team-name-link h3 {
  text-decoration: none;
  display: inline-block;
  padding: 10px 16px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: #42b983;
  background-color: transparent;
  transition: background-color 0.3s;
}

.team-name-link h3:hover {
  background-color: #20c997;
  color: #fff;
}
</style>